.nav-mobile {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  padding: 0 10px;
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 0 1px rgb(0 0 0 / 10%);
  border-bottom: 1px solid #f0f0f0;
  background-color: #fff;
  box-sizing: border-box;
}

.nav-mobile-back {
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-mobile-back > img {
  width: 60px;
  height: 60px;
}

.nav-mobile-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 30px;
  color: #333;
}
